import React, { useState } from "react";
function Cinema_Seating() {
  const [silverseat, setSilverseat] = useState("");
  const [goldseat, setGoldseat] = useState("");
  const [cost, setCost] = useState(0);

  const select_seat = (e) => {
    console.log(e.dataset.price);
    if (
      e.style.backgroundColor == "rgb(172, 253, 90)" ||
      e.style.backgroundColor == "rgb(80, 231, 224)"
    ) {
      e.style.backgroundColor = "white";
    } else if (e.dataset.price == 300) {
      e.style.backgroundColor = "rgb(172, 253, 90)";
    } else {
      e.style.backgroundColor = "rgb(80, 231, 224)";
    }

    let blue = 0,
      green = 0;
    var seats = document.getElementsByClassName("seats");
    for (let i = 0; i < seats.length; i++) {
      if (seats[i].style.backgroundColor == "rgb(172, 253, 90)") {
        green = green + 1;
      }
      if (seats[i].style.backgroundColor == "rgb(80, 231, 224)") {
        blue = blue + 1;
      }
    }
    console.log(green, blue);
    setCost(green * 300 + blue * 500);

    let silver_seat = [],
      gold_seat = [];
    for (let i = 0; i < seats.length; i++) {
      if (seats[i].style.backgroundColor == "rgb(172, 253, 90)") {
        silver_seat.push(seats[i].innerText);
      }
      if (seats[i].style.backgroundColor == "rgb(80, 231, 224)") {
        gold_seat.push(seats[i].innerText);
      }
    }

    setGoldseat(
      gold_seat
        .sort(function (a, b) {
          return a - b;
        })
        .toString()
    );
    setSilverseat(
      silver_seat
        .sort(function (a, b) {
          return a - b;
        })
        .toString()
    );
  };

  const create_seat = (n, price) => {
    var seats = [];

    if (price == 500) {
      for (let i = 1; i <= n; i++) {
        seats.push(
          <div
            key={i}
            data-price={price}
            onClick={(e) => {
              select_seat(e.target);
            }}
            className="seats"
          >
            G{i}
          </div>
        );
      }
    }
    if (price == 300) {
      for (let i = 1; i <= n; i++) {
        seats.push(
          <div
            key={i}
            data-price={price}
            onClick={(e) => {
              select_seat(e.target);
            }}
            className="seats"
          >
            S{i}
          </div>
        );
      }
    }

    return seats;
  };
  return (
    <div className="theatre">
      <div>
        <h1>Select Seats</h1>
        <h2>Gold Seat for ₹ 500</h2>
        <div className="seating_arrangement">
          <div className="seat_container gold_seat">{create_seat(40, 500)}</div>
        </div>
        <h2>Silver Seat for ₹ 300</h2>

        {/* {
      if(mobile){

      }} */}

        <div className="seating_arrangement2">
          <div className="seat_container silver_seat1">
            {create_seat(55, 300)}
          </div>
          <div className="seat_container silver_seat2">
            {create_seat(55, 300)}
          </div>
        </div>

        <div className="screen">
          <h1>Screen</h1>
        </div>
        <div>
          <h2>Total ₹ {cost}</h2>
          <h3>
            Seats Selected : <span id="gs">{goldseat}</span> <br />{" "}
            <span id="ss">{silverseat}</span>
          </h3>
        </div>
      </div>
    </div>
  );
}
export default Cinema_Seating;
